import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { Formik, Form, Field } from 'formik'
import { TextField } from 'formik-material-ui'
import * as Yup from 'yup'
import MaterialTable from 'material-table'
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  makeStyles,
  useMediaQuery,
  useTheme,
} from '@material-ui/core'
import { Grade, ShoppingCart } from '@material-ui/icons'

import ActionButton from '../ActionButton'
import ListErrors from '../ListErrors'
import StudentsTable from './StudentsTable'
import agent from '../../agent'

import {
  ADD_TRANSACTIONS,
  TRANSACTIONS_DIALOG_UNLOADED,
} from '../../constants/actionTypes'

const useStyles = makeStyles((theme) => ({
  dialog: {
    '& > .MuiDialog-container > .MuiPaper-root': {
      [theme.breakpoints.up('lg')]: {
        width: theme.spacing(80),
      },
    },
  },
}))

const mapStateToProps = (state) => ({
  ...state.scores,
})

const mapDispatchToProps = (dispatch) => ({
  onAdd: (transactions) =>
    dispatch({
      type: ADD_TRANSACTIONS,
      payload: agent.Transactions.addMany(transactions),
      snackbar: {
        message: `${transactions.typeTitle} added`,
        variant: 'success',
      },
    }),
  onExit: () => dispatch({ type: TRANSACTIONS_DIALOG_UNLOADED }),
})

const BulkTransactionsDialog = ({
  course,
  errors,
  inProgress,
  onAdd,
  onExit,
  type,
}) => {
  const [open, setOpen] = useState(false)
  const [students, setStudents] = useState([])

  const theme = useTheme()
  const fullScreen = useMediaQuery(theme.breakpoints.down('md'))
  const classes = useStyles()

  const handleClickOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  const handleSubmitAdd = (values) => {
    onAdd({
      ...values,
      students: students.map((student) => student._id),
      type: type === 'Bonuses' ? 'bonus' : 'purchase',
      typeTitle: type,
    })
  }

  const handleStudentSelect = (rows) => {
    setStudents(rows)
  }

  useEffect(() => {
    // Close the dialog only if there are no errors
    if (!inProgress && !errors) setOpen(false)
  }, [inProgress])

  return (
    <>
      <ActionButton
        action={`Add ${type.toLowerCase()}`}
        onClick={handleClickOpen}
      >
        {type === 'Bonuses' && <Grade />}
        {type === 'Purchases' && <ShoppingCart />}
      </ActionButton>

      <Dialog
        className={classes.dialog}
        fullScreen={fullScreen}
        onClick={(ev) => ev.stopPropagation()}
        onClose={handleClose}
        onExit={onExit}
        onFocus={(ev) => ev.stopPropagation()}
        open={open}
      >
        <Formik
          initialValues={{
            amount: '',
            description: '',
          }}
          validationSchema={Yup.object({
            amount: Yup.number().typeError('Numbers only').required('Required'),
            description: Yup.string().required('Required'),
          })}
          onSubmit={async (values) => {
            handleSubmitAdd(values)
          }}
        >
          <Form>
            <DialogTitle>Add {type}</DialogTitle>
            <DialogContent>
              <ListErrors errors={errors} />
              <Grid container spacing={2}>
                <Grid item xs={12} md={6}>
                  <Field
                    autoFocus
                    color='primary'
                    component={TextField}
                    disabled={inProgress}
                    label='Description'
                    name='description'
                    type='text'
                    fullWidth
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <Field
                    color='primary'
                    component={TextField}
                    disabled={inProgress}
                    label='Amount'
                    name='amount'
                    type='text'
                    fullWidth
                  />
                </Grid>
                <Grid item xs={12}>
                  <StudentsTable
                    course={course}
                    onSelect={handleStudentSelect}
                  />
                </Grid>
              </Grid>
            </DialogContent>
            <DialogActions>
              <Button
                disabled={inProgress || students.length === 0}
                color='primary'
                type='submit'
                variant='contained'
              >
                {inProgress ? 'Adding...' : 'Add'}
              </Button>
              <Button
                disabled={inProgress}
                onClick={handleClose}
                variant='contained'
              >
                Cancel
              </Button>
            </DialogActions>
          </Form>
        </Formik>
      </Dialog>
    </>
  )
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(BulkTransactionsDialog)
