import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
import { Grid, LinearProgress, Paper, makeStyles } from '@material-ui/core'
import Div100vh from 'react-div-100vh'

import Logo from './../Logo'
import LoginForm from './LoginForm'
import background from './../../images/chemistry-background.jpg'

import { LOGIN_PAGE_UNLOADED } from '../../constants/actionTypes'

const useStyles = makeStyles((theme) => ({
  logo: {
    display: 'block',
    margin: '0 auto',
    width: 120,
  },
  page: {
    alignItems: 'center',
    backgroundImage: `url(${background})`,
    backgroundSize: 'cover',
    display: 'flex',
    height: '100%',
    justifyContent: 'center',
    [theme.breakpoints.up('lg')]: {
      backgroundSize: 'initial',
      justifyContent: 'flex-end',
    },
  },
  panel: {
    borderRadius: '1rem',
    overflowY: 'scroll',
    padding: '4rem 2rem',
    position: 'relative',
    width: '80%',
    [theme.breakpoints.up('lg')]: {
      borderRadius: '0',
      height: '100%',
      width: '40%',
    },
  },
  progress: {
    height: 10,
    left: '0',
    position: 'absolute',
    top: '0',
    width: '100%',
  },
}))

const mapStateToProps = (state) => ({
  ...state.auth,
  currentUser: state.common.currentUser,
})

const mapDispatchToProps = (dispatch) => ({
  onUnload: () => dispatch({ type: LOGIN_PAGE_UNLOADED }),
})

const Login = (props) => {
  const classes = useStyles()

  useEffect(() => {
    return () => props.onUnload()
  }, [])

  if (props.currentUser) {
    return <Redirect to='/' />
  }

  return (
    <Div100vh>
      <div className={classes.page}>
        <Paper elevation={12} className={classes.panel}>
          {props.inProgress && <LinearProgress className={classes.progress} />}
          <Grid container spacing={4}>
            <Grid item xs={12}>
              <Logo className={classes.logo} />
            </Grid>
            <Grid item xs={12}>
              <LoginForm />
            </Grid>
          </Grid>
        </Paper>
      </div>
    </Div100vh>
  )
}

export default connect(mapStateToProps, mapDispatchToProps)(Login)
