import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import {
  Button,
  Checkbox,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Fab,
  FormControl,
  FormControlLabel,
  FormGroup,
  makeStyles,
  useMediaQuery,
  useTheme,
  Typography,
} from '@material-ui/core'
import { Alert } from '@material-ui/lab'
import { Publish } from '@material-ui/icons'

import Filedrop from '../Filedrop'
import ListErrors from '../ListErrors'
import Spinner from '../Spinner'
import agent from '../../agent'

import {
  IMPORT_SCORES,
  IMPORT_SCORES_DIALOG_UNLOADED,
} from '../../constants/actionTypes'

const useStyles = makeStyles((theme) => ({
  courses: {
    '& div': {
      'boxShadow': theme.shadows[4],
      'cursor': 'pointer',
      'marginBottom': theme.spacing(1),
      'padding': `${theme.spacing(3)}px ${theme.spacing(4)}px`,
      'transition': 'all 0.15s ease-in-out',
      '&:focus': {
        backgroundColor: theme.palette.primary.main,
        boxShadow: theme.shadows[1],
      },
      '&:hover': {
        backgroundColor: theme.palette.primary.light,
        boxShadow: theme.shadows[1],
      },
    },
  },
  dialog: {
    '& > .MuiDialog-container > .MuiPaper-root': {
      [theme.breakpoints.up('lg')]: {
        width: theme.spacing(100),
      },
    },
  },
  icon: {
    marginRight: theme.spacing(1),
  },
}))

const mapStateToProps = (state) => ({
  ...state.scores,
  courses: state.courses.courses,
})

const mapDispatchToProps = (dispatch) => ({
  onImport: (data) =>
    dispatch({
      type: IMPORT_SCORES,
      payload: agent.Scores.import(data),
      snackbar: { message: 'Scores imported', variant: 'success' },
    }),
  onExit: () => dispatch({ type: IMPORT_SCORES_DIALOG_UNLOADED }),
})

const ScoreImportStepper = ({ courses, onDataChange, onShowImport }) => {
  const [file, setFile] = useState(null)
  const [course, setCourse] = useState(null)
  const [tasks, setTasks] = useState(null)

  const classes = useStyles()

  const handleCourseSelect = (slug) => {
    setCourse(slug)
  }

  const handleFiledrop = (file) => {
    const tasks = {}
    Object.keys(file[0])
      .slice(1)
      .forEach((task) => {
        tasks[task] = false
      })
    setFile(file)
    setTasks(tasks)
  }

  const handleTaskChange = (ev) => {
    setTasks({ ...tasks, [ev.target.name]: ev.target.checked })
  }

  // Determine if any tasks have been checked/selected
  useEffect(() => {
    const tasksSelected = tasks
      ? Object.values(tasks).some((el) => el === true)
      : false
    onShowImport(tasksSelected)
  }, [tasks])

  // Keep the imported data object current
  useEffect(() => {
    onDataChange({ file, course, tasks })
  }, [file, course, tasks])

  return (
    <>
      {!file && !course && !tasks && (
        <div>
          <Typography gutterBottom>
            Scores must be imported from a .csv file exported from Infinite
            Campus
          </Typography>
          <Filedrop onFiledrop={handleFiledrop} />
        </div>
      )}

      {file && !course && tasks && (
        <div>
          <Typography gutterBottom variant='body1'>
            Select the course into which you would like to import
          </Typography>
          {courses.length > 0 ? (
            <div className={classes.courses}>
              {courses.map((course) => (
                <div
                  key={course.slug}
                  onClick={() => {
                    handleCourseSelect(course.slug)
                  }}
                  tabIndex='0'
                >
                  <Typography variant='body1'>
                    <b>{course.name}</b> &middot; Period {course.period}
                  </Typography>
                </div>
              ))}
            </div>
          ) : (
            <Alert severity='info'>
              Please create a course before importing scores
            </Alert>
          )}
        </div>
      )}

      {file && course && tasks && (
        <FormControl component='fieldset'>
          <Typography gutterBottom variant='body1'>
            Select the assignments to import
          </Typography>
          <FormGroup>
            {Object.keys(tasks).map((task) => (
              <FormControlLabel
                control={
                  <Checkbox
                    checked={tasks[task]}
                    onChange={handleTaskChange}
                    name={task}
                  />
                }
                key={task}
                label={task}
              />
            ))}
          </FormGroup>
        </FormControl>
      )}
    </>
  )
}

const ImportScoresDialog = ({
  courses,
  errors,
  inProgress,
  onExit,
  onImport,
}) => {
  const [open, setOpen] = useState(false)
  const [ready, setReady] = useState(false)
  const [data, setData] = useState(null)

  const theme = useTheme()
  const fullScreen = useMediaQuery(theme.breakpoints.down('md'))
  const classes = useStyles()

  const handleClickOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  const handleDataChange = (importData) => {
    setData(importData)
  }

  const handleShowImport = (selected) => {
    setReady(selected)
  }

  const handleClickImport = () => {
    // Get student usernames (student numbers)
    const usernames = data.file
      .map(
        (student, index) =>
          index > 1 && student['Assignment Name'].split('#').pop()
      )
      .filter((username) => username !== false)

    // Prepare data to send to server
    const parsed = { course: data.course, tasks: [] }
    for (const taskName in data.tasks) {
      const selected = data.tasks[taskName]
      if (selected) {
        const task = {
          title: taskName,
          totalPoints: data.file[1][taskName],
          scores: {},
        }

        for (const username of usernames) {
          const student = data.file.find((row) =>
            row['Assignment Name'].endsWith(username)
          )
          if (student) task.scores[username] = student[taskName]
        }

        parsed.tasks.push(task)
      }
    }

    onImport(parsed)
  }

  useEffect(() => {
    // Close the dialog only if there are no errors
    if (!inProgress && !errors) setOpen(false)
  }, [inProgress])

  return (
    <>
      <Fab color='primary' onClick={handleClickOpen} variant='extended'>
        <Publish className={classes.icon} />
        Import scores
      </Fab>

      <Dialog
        className={classes.dialog}
        fullScreen={fullScreen}
        onClick={(ev) => ev.stopPropagation()}
        onClose={handleClose}
        onExit={onExit}
        onFocus={(ev) => ev.stopPropagation()}
        open={open}
      >
        <DialogTitle>Import Scores</DialogTitle>
        <DialogContent>
          <ListErrors errors={errors} />
          {inProgress ? (
            <Spinner message='Importing...' />
          ) : (
            <ScoreImportStepper
              courses={courses}
              onDataChange={handleDataChange}
              onShowImport={handleShowImport}
            />
          )}
        </DialogContent>
        <DialogActions>
          {ready && (
            <Button
              color='primary'
              disabled={inProgress}
              onClick={handleClickImport}
              variant='contained'
            >
              {inProgress ? 'Importing...' : 'Import'}
            </Button>
          )}
          <Button
            disabled={inProgress}
            onClick={handleClose}
            variant='contained'
          >
            Cancel
          </Button>
        </DialogActions>
      </Dialog>
    </>
  )
}

export default connect(mapStateToProps, mapDispatchToProps)(ImportScoresDialog)
